

















import {Vue, Component, Prop, Watch} from 'vue-property-decorator';
import {NovelInfo} from '@/model/itemInfo.ts';
import {scrollTopDialog, scrollRightDialog} from '@/model/commonUtil.ts';

@Component({
  components:{
  }
})
export default class DialogNovel extends Vue{
  @Prop()
  novel!: NovelInfo;
  @Prop()
  isInside = false;
  @Prop()
  isVertical = false;

  private titleSuffix = "";
  updated(){
    this.isVertical ? scrollRightDialog(): scrollTopDialog();
  }
}

