







import {Vue, Component} from 'vue-property-decorator'
import {EnumTitles} from '@/model/make1Titles.ts';
import Make1PageTop from '@/views/make1-page-top.vue';
import TmpPage from '@/views/make1mains/temporary.vue';
import {scrollTopMain} from '@/model/commonUtil.ts';

import CCPage from '@/views/make1mains/crystal-connection.vue';
import NCPage from '@/views/make1mains/narcissism.vue';
import YTPage from '@/views/make1mains/your-town.vue';
import WMPage from '@/views/make1mains/weld-mask.vue';
import FGPage from '@/views/make1mains/forest-god.vue';
import AAPage from '@/views/make1mains/arameliart.vue';
import VTPage from '@/views/make1mains/vailtrath.vue';

@Component({
  components:{
    Make1PageTop,
    TmpPage,
    CCPage,
    NCPage,
    YTPage,
    WMPage,
    FGPage,
    AAPage,
    VTPage
  }
})
export default class Make1Page extends Vue{
  private currentTitle:any = Make1PageTop;
   
  contentClick(showVal:number){
    scrollTopMain();
    switch(showVal){
      case EnumTitles.TOP: this.currentTitle = Make1PageTop; break;
      case EnumTitles.TMP: this.currentTitle = TmpPage;break;
      case EnumTitles.YT:  this.currentTitle = YTPage; break;
      case EnumTitles.CC:  this.currentTitle = CCPage; break;
      case EnumTitles.NC:  this.currentTitle = NCPage; break;
      case EnumTitles.WM:  this.currentTitle = WMPage; break;
      case EnumTitles.FG:  this.currentTitle = FGPage; break;
      case EnumTitles.AA:  this.currentTitle = AAPage; break;
      case EnumTitles.VT: this.currentTitle = VTPage; break;
    }
  }
}
